body{
    font-family: 'Lora', sans-serif;
    //background: #f8f9fa;
    //background: #58A06D;
    //background: url(https://www.vexer.com.br/wp-content/uploads/2021/06/agua-da-chuva-e-pura.jpg);
    //background-size: cover;
    background-color: #deebdd;
    background-image: linear-gradient(
87deg, #819C70 0%, #E3EDB9 100%);
    //background-image: url('../images/sf-logo.png');
}

.main{
    //max-width: 880px;
    background: #f3f3f3;
    padding: 50px;
    max-width: 1200px;
    margin: 50px auto;
}

.logo{
    display: inline-block;
    margin: 0 0 0 -16px;
    span{
        display: none;
    }
}

.intro{
    font-size: 22px;
    margin-top: 100px;
    margin-bottom: 80px;
    max-width: 1000px;

    .nome{
        display: block;
        font-size: 90px;
        font-weight: bold;
        color: #485152;
        margin-bottom: 40px;
    }

    .intro__info > p {
        line-height: 35px;
    }
}

.conteudo{

    h2{
        font-size: 40px;
        margin-bottom: 20px;
        color: #485152;
    }
}

.posts{
    display: flex;
    justify-content: space-between;
    .post{
        padding: 20px;
        background: RGBA(255,255,255,.75);
        width: calc(33.33% - 60px);

        a{
            text-decoration: none;
            font-size: 24px;
            color: #EA9F62;
            transition: color .5s;
            &:hover{
                color: #819C70;
            }
        }
        h3{
            margin-top: 0;
        }
        h4{
            font-weight: 500;
            margin-top: 0;
            margin-bottom: 0;
            line-height: 25px;
        }
    }
}


.post-content{
    margin-top: 70px;
    max-width: 800px;
    font-size: 20px;

    > p, > ul li{
        line-height: 34px;
    }

    p a{
        text-decoration:none;
        color:#EA9F62;
        transition: color .5s;
        &:hover{
            color: #819C70;
        }
    }

    h1{
        font-size: 60px;
        color: #485152;
    }

    h2{
        margin-bottom: 5px;   
    }
    h3{
        color: #464646;
        font-weight: 600;
    }
    h2+h3{
        margin-top: 0;
    }
}